import React from "react"
import Layout from "../components/layout"
import { Helmet } from "react-helmet"
import { Link } from "gatsby"
import SEO from "../components/seo"

const HpcBudgeting = () => {
  return (
    <Layout>
      <Helmet title={"Hpc Budget Manager"}>
        <link href="https://unpkg.com/nes.css@latest/css/nes.min.css" rel="stylesheet" type="text/css" />
      </Helmet>
      <SEO title="Hpc Budget Manager" />
      <div className="flex-column">
        <Link
          to="/"
          style={{
            color: `rgb(247, 140, 108)`,
            fontFamily: `Montserrat, sans-serif`,
            fontSize: `1.3rem`,
            fontWeight: 800,
            marginBottom: `1rem`,
            textDecoration: `none`,
          }}
        >
          Andrew Stolzle
        </Link>
        <article className="flex-column">
          <h1 style={{ color: `#50fa7b`, fontSize: `2rem` }}>HPC Budget Manager</h1>
          <div className="flex-row-wrap">
            <div className="boxed-text">AngularJS</div>
            <div className="boxed-text">TypeScript</div>
            <div className="boxed-text">Material UI</div>
            <div className="boxed-text">Telerik Kendo</div>
          </div>
          <p>Detailed writeup coming soon.</p>
          <figure>
            <img src={require("../images/registration.png")} alt="user registration" />
            <figcaption>Registration</figcaption>
          </figure>
          <figure>
            <img src={require("../images/grid.png")} alt="user grid" />
            <figcaption>User grid</figcaption>
          </figure>
        </article>
      </div>
    </Layout>
  )
}

export default HpcBudgeting
